body {
  text-align: center;
  width: 100vw;
  font-weight: 700;
  font-family: 'Montserrat', sans-serif;
}
#fly-in {
 
  text-transform: uppercase;
}
#fly-in p {
    font-size: 3em;
}

#fly-in div {
 position: fixed; 
  margin: 2vh 0;
  opacity: 0;
  width: 100vw;
  -webkit-animation: switch 15s linear 1;
          animation: switch 15s linear 1;
}
#fly-in div:nth-child(1) { font-size: 4em}
#fly-in div:nth-child(2) { font-size: 4em;-webkit-animation-delay: 2s;animation-delay: 2s}
#fly-in div:nth-child(3) { font-size: 2.5em;-webkit-animation-delay: 4s;animation-delay: 4s}
#fly-in div:nth-child(4) { font-size: 4em;-webkit-animation-delay: 6s;animation-delay: 6s}
@media (max-width: 768px) {
    #fly-in div{
        margin-top:-15vh;
        font-size: 1.5em;
        width:100vw;
        background: -webkit-linear-gradient(45deg, #FF9933 ,  #fafafa , #138808 );
  -webkit-background-clip: text;
          background-clip: text;
 -webkit-text-fill-color: transparent;
    }
    #fly-in div:nth-child(1) { font-size: 3em}
#fly-in div:nth-child(2) { font-size: 3em;-webkit-animation-delay: 2s;animation-delay: 2s}
#fly-in div:nth-child(3) { font-size: 3em;-webkit-animation-delay: 4s;animation-delay: 4s}
#fly-in div:nth-child(4) { font-size: 3em;-webkit-animation-delay: 6s;animation-delay: 6s}  
}
@media (max-width: 1280px) and (min-width: 768px) {
    #fly-in div{
        margin-top:-15vh;
        font-size: 1.5em;
        width:100vw;
