    width: 60vw;
    height: 60vh;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px grey;
    background: pink;
    
    position: relative
}

.Homepage > .button {
    background: #18f2ca;
    border: 1px solid #1877f2;
    color: rgb(255, 255, 255);
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}
